import styled from '@emotion/styled'

import { breakpoints } from '../../utils/styles'


export const Grid = styled.div`
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2.5rem;
  justify-items: center;

  @media (max-width: ${breakpoints.s}px){
    grid-template-columns: repeat(1, 1fr);
  }
`

export const Product = styled.div`
  display: flex;
  min-height: 100%;
  flex-direction: column;
  transition: .3s ease;
  img {
      -webkit-transition: all .8s cubic-bezier(0.19, 1, 0.22, 1)!important;
      -moz-transition: all .8s cubic-bezier(0.19, 1, 0.22, 1)!important;
      -ms-transition: all .8s cubic-bezier(0.19, 1, 0.22, 1)!important;
      -o-transition: all .8s cubic-bezier(0.19, 1, 0.22, 1)!important;
      transition: all .8s cubic-bezier(0.19, 1, 0.22, 1)!important;
  }
  &:hover img {
    z-index: 900;
    transform:translateY(-35px);
  }
`

export const Title = styled.span`
  font-weight: 300;
  font-size: 1.4rem;
  text-align: center;
  text-transform: uppercase;
`

export const PriceTag = styled.span`
  font-weight: 300;
  font-size: 1.4rem;
  text-align: center;
  margin-top: 15px;

  :before {
    content: ''
  }
`