import React, { useContext, useState, Component } from 'react'
import { useStaticQuery, graphql, Link } from 'gatsby'
import PropTypes from 'prop-types'

import {Navbar, Nav, Modal, Form, FormControl, Button, DropdownButton, Dropdown} from 'react-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faShoppingBag, faUser, faSearch } from '@fortawesome/free-solid-svg-icons'

import { Img } from '~/utils/styles'

import ProductPopover from '~/components/ProductPopover'
import ProductGrid from '~/components/ProductGrid'
import { Grid } from '~/components/ProductGrid/styles'
import ProductGridItem from '~/components/ProductGridItem'
import AniLink from "gatsby-plugin-transition-link/AniLink"

export class NavSearchDropdown extends Component {
    constructor(props) {
        super(props);
        this.state = {
            document: "",
        };
        console.log(this.props)
    }
    componentDidMount() {
        this.setState({
            document: ""
        });
    }

    render() {

        const { edges: products } = document.props.data.allShopifyProduct

        return(
            <Form inline action="../search" method="GET">
                <div className={'input-group input-group-sm'}>
                    <FormControl type="text" placeholder="Search"
                                 className="form-control-sm" name="value" type="text"  placeholder="Search"
                                 value={query}  value={this.state.document} onChange={e => this.setState({ document: e.target.value })}/>
                    <div className={'input-group-append'}>
                        <Button variant="btn-danger btn-sm"><FontAwesomeIcon className="has-text-dark is-size-5" icon={faSearch} /></Button>
                    </div>
                </div>
                <div className="columns is-multiline ">
                    {products
                        .filter(p =>
                            p.node.title.toUpperCase().includes(this.state.document.toUpperCase()) ||
                            p.node.productType.toUpperCase().includes(this.state.document.toUpperCase()) ||
                            (p.node.title.toUpperCase().includes(this.state.document.toUpperCase()) &&
                                p.node.productType.toUpperCase().includes(this.state.document.toUpperCase()))
                        )
                        .map((p, i) => {

                            return (
                                !p ?
                                    <p>Nothing with : {this.state.document} </p>
                                    :
                                    <div className="column is-3" style={{marginBottom: "40px"}} key={i}>
                                        <ProductGridItem product={p}/>
                                    </div>
                            )
                        })}
                </div>
            </Form>
        )
    }
}

export default NavSearchDropdown

NavSearchDropdown.propTypes = {
    data: PropTypes.shape({
        allShopifyProduct: PropTypes.object
    }),
}

export const query = graphql`
    query {
        allShopifyProduct(
            sort: {
                fields: [createdAt]
                order: DESC
            }
        ) {
            edges {
                node {
                    id
                    title
                    handle
                    createdAt(fromNow: true)
                    publishedAt
                    productType
                    vendor
                    priceRange {
                        maxVariantPrice {
                            amount
                        }
                    }
                    images {
                        originalSrc
                        id
                        localFile {
                            childImageSharp {
                                fluid(maxWidth: 100) {
                                    ...GatsbyImageSharpFluid_noBase64
                                }
                            }
                        }
                    }
                    variants {
                        id
                        title
                        price
                    }
                }
            }
        }
    }
`
