import React, { useContext }  from 'react';
import Img from "gatsby-image"
import { Link } from "gatsby" /* eslint-disable */
import {
    Grid,
    Product,
    Title,
    PriceTag
} from './styles'

const ProductBox = props => {
    const product = props.product
    return (

    <Product key={product.node.title} style={{width:'20rem'}} className={'d-flex flex-column'}>
        <Link to={`/product/${product.node.handle}/`} swipe direction="left" hex="#ffffff"
                 className={'w-100 mb-auto'}
                 entryOffset={50}
                 duration={0.5}
                 top="enter"
                 bg="#cc1130"
                 style={{overflow:'visible',div:'{overflow:visible}'}}>
            <Img
                fluid={product.node.images[0].localFile.childImageSharp.fluid}
                key={product.node.images[0].localFile.id}
                alt={product.node.title}
            />
        </Link>
        <div className={'d-flex flex-column'}>
            <Title>{product.node.title}</Title>
            <PriceTag>${product.node.variants[0].price}</PriceTag>
        </div>
    </Product>
    );
};

export default ProductBox;

